<script>
export default {
  name: "Home",
  components: {},
  data() {
    return {
      title: "",
      nocom: false,
      views: "",
      loadData: false,
      msg: "",
      getData: [],
      typing: false,
      attrs: "",
      // url: "http://localhost:3000/videos/" + this.$route.params.id + ".mp4",
      // videoOptions: {
      //   autoplay: false,
      //   controls: true,
      //   // width: "100%",
      //   height: "400px",
      //   with: "100%",
      //   sources: [
      //     {
      //       src:
      //         "http://localhost:3000/videos/" + this.$route.params.id + ".mp4",
      //       type: "video/mp4",
      //     },
      //   ],
      // },
    };
  },
  sockets: {
    connect: function() {
      console.log("socket connected");
    },

    recev: function(data) {
      this.soundEffect.play();
      this.getData.push(data);
      // this.scrollBottom();
      // this.lenData = false;
      // this.getHeadsss();
      this.nocom = false;
    },

    // connect_error: async (err) => {
    //   console.log(err);
    //   // this.logout;
    // },

    typings(data) {
      clearTimeout(this.timeOutId);
      this.typing = true;
      this.typs = data.nickname;
      this.timeOutId = setTimeout(() => {
        this.typing = false;
      }, 2000);
      return data;
    },
  },
  metaInfo() {
    return {
      title: this.title,
      meta: [
        {
          name: "description",
          content: this.title,
        },
        {
          property: "og:title",
          content: this.title,
        },
        { property: "og:site_name", content: "videos" },
        { property: "og:type", content: "website" },
        { name: "robots", content: "index,follow" },
      ],
    };
  },
  methods: {
    emitTyping() {
      // this.scrollBottom();
      this.$socket.emit("typing", {
        nickname: this.$store.getters.getUserName,
      });
    },

    sendMessage: async function() {
      if (this.msg.trim() != "") {
        this.$socket.emit("add", {
          text: this.msg,
          userId: this.$store.getters.getUserId,
          username_u: this.$store.getters.getUserName,
          dateCreate: this.$moment().format("YYYY-MM-DD HH:mm:ss"),
          groupe: this.$route.params.id,
        });

        this.lenData = false;
        this.getData.push({
          text: this.msg,
          userId: this.$store.getters.getUserId,
          username_u: this.$store.getters.getUserName,
          dateCreate: this.$moment().format("YYYY-MM-DD HH:mm:ss"),
        });

        this.sendInDataBase(this.msg);
        this.nocom = false;
        this.msg = "";
      } else {
        //  empty
      }
    },
    sendInDataBase(msg) {
      // let AuthStr = "Bearer ".concat(this.$store.getters.getToken);
      let formData = new FormData();
      formData.append("text", msg);
      formData.append("userId", this.$store.getters.getUserId);
      formData.append("postId", this.$route.params.id);
      formData.append("username_u", this.$store.getters.getUserName);

      this.$http
        .post("/comments/add", formData, {
          headers: {
            Accept: "application/json,*/*",
            "Content-Type": "application/json",
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers": "Authorization",
            Authorization: "Bearer " + this.$store.getters.getToken,
          },
        })
        .then((res) => {
          return res;
        })
        .catch((err) => {
          alert(err);
        });
    },
    // gow(param) {
    //   // alert(param.data);
    //   this.dataPosts(param.data);
    // },
    dataPosts: function(param) {
      this.$http
        .get("/posts/watch/" + param)
        .then((res) => {
          this.loadData = true;
          this.title = res.data.title;
          this.views = res.data.views;
          this.url = res.data.link;
          this.poster = `/thumbnail/${res.data.imagepost}`;
          this.userChaine = res.data.user[0];

          // alert(res.data.link);
        })
        .catch((err) => {
          return err;
        });
    },
  },
  mounted() {
    this.dataPosts(this.$route.params.id);
    this.$socket.emit("joinGroup", this.$route.params.id);
    this.$http.get("/comments/read/" + this.$route.params.id).then((res) => {
      if (res.data.length == 0) {
        this.nocom = true;
      } else {
        this.getData = res.data;
      }
    });
  },
};
</script>
